"use client";

import { useState, useEffect } from "react";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Textarea } from "@/components/ui/textarea";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Badge } from "@/components/ui/badge";
import { useForm } from "react-hook-form";
import { toast } from "sonner";
import { Toaster } from "@/components/ui/sonner";

interface Microfinance {
  id: string;
  name: string;
  description: string;
  address: string;
  phone: string;
  email: string;
  createdAt: string;
}

interface Member {
  id: string;
  microfinanceId: string;
  firstName: string;
  lastName: string;
  idNumber: string;
  phone: string;
  email: string;
  address: string;
  status: 'Pending' | 'Active';
  microfinanceName?: string;
  createdAt: string;
}

interface Loan {
  id: string;
  memberId: string;
  loanType: 'Emergency' | 'Development' | 'Business' | 'Education';
  amount: number;
  repaymentPeriod: number;
  status: 'Pending' | 'Approved' | 'Rejected';
  memberName?: string;
  microfinanceName?: string;
  createdAt: string;
}

export default function SACCOAdmin() {
  const [microfinances, setMicrofinances] = useState<Microfinance[]>([]);
  const [members, setMembers] = useState<Member[]>([]);
  const [loans, setLoans] = useState<Loan[]>([]);
  const [loading, setLoading] = useState(false);

  const microfinanceForm = useForm();
  const memberForm = useForm();
  const loanForm = useForm();

  // Fetch data functions
  const fetchMicrofinances = async () => {
    try {
      const response = await fetch('/api/microfinances');
      const data = await response.json();
      setMicrofinances(data);
    } catch (error) {
      toast.error('Failed to fetch microfinances');
    }
  };

  const fetchMembers = async () => {
    try {
      const response = await fetch('/api/members');
      const data = await response.json();
      setMembers(data);
    } catch (error) {
      toast.error('Failed to fetch members');
    }
  };

  const fetchLoans = async () => {
    try {
      const response = await fetch('/api/loans');
      const data = await response.json();
      setLoans(data);
    } catch (error) {
      toast.error('Failed to fetch loans');
    }
  };

  useEffect(() => {
    fetchMicrofinances();
    fetchMembers();
    fetchLoans();
  }, []);

  // Form submission handlers
  const onSubmitMicrofinance = async (data: any) => {
    setLoading(true);
    try {
      const response = await fetch('/api/microfinances', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(data),
      });

      if (response.ok) {
        toast.success('Microfinance created successfully');
        microfinanceForm.reset();
        fetchMicrofinances();
      } else {
        const error = await response.json();
        toast.error(error.error || 'Failed to create microfinance');
      }
    } catch (error) {
      toast.error('Failed to create microfinance');
    }
    setLoading(false);
  };

  const onSubmitMember = async (data: any) => {
    setLoading(true);
    try {
      const response = await fetch('/api/members', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(data),
      });

      if (response.ok) {
        toast.success('Member registered successfully');
        memberForm.reset();
        fetchMembers();
      } else {
        const error = await response.json();
        toast.error(error.error || 'Failed to register member');
      }
    } catch (error) {
      toast.error('Failed to register member');
    }
    setLoading(false);
  };

  const onSubmitLoan = async (data: any) => {
    setLoading(true);
    try {
      const response = await fetch('/api/loans', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(data),
      });

      if (response.ok) {
        toast.success('Loan application submitted successfully');
        loanForm.reset();
        fetchLoans();
      } else {
        const error = await response.json();
        toast.error(error.error || 'Failed to submit loan application');
      }
    } catch (error) {
      toast.error('Failed to submit loan application');
    }
    setLoading(false);
  };

  const activateMember = async (memberId: string) => {
    setLoading(true);
    try {
      const response = await fetch('/api/members/activate', {
        method: 'PUT',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ memberId }),
      });

      if (response.ok) {
        toast.success('Member activated successfully');
        fetchMembers();
      } else {
        const error = await response.json();
        toast.error(error.error || 'Failed to activate member');
      }
    } catch (error) {
      toast.error('Failed to activate member');
    }
    setLoading(false);
  };

  // Get active members for loan form
  const activeMembers = members.filter(member => member.status === 'Active');

  return (
    <div className="min-h-screen bg-gray-50">
      <Toaster />
      
      {/* Header */}
      <div className="bg-white shadow-sm border-b">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center py-6">
            <div>
              <h1 className="text-3xl font-bold text-gray-900">SACCO Admin</h1>
              <p className="text-gray-600">Microfinance Management System</p>
            </div>
          </div>
        </div>
      </div>

      {/* Dashboard Stats */}
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <div className="grid grid-cols-1 md:grid-cols-4 gap-6 mb-8">
          <Card>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium">Total Microfinances</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">{microfinances.length}</div>
              <p className="text-xs text-muted-foreground">Active SACCO organizations</p>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium">Active Members</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">{members.filter(m => m.status === 'Active').length}</div>
              <p className="text-xs text-muted-foreground">{members.filter(m => m.status === 'Pending').length} pending activation</p>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium">Pending Loans</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">{loans.filter(l => l.status === 'Pending').length}</div>
              <p className="text-xs text-muted-foreground">Awaiting approval</p>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium">Total Approved Loans</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">KSh {loans.filter(l => l.status === 'Approved').reduce((sum, loan) => sum + loan.amount, 0).toLocaleString()}</div>
              <p className="text-xs text-muted-foreground">Cumulative loan amount</p>
            </CardContent>
          </Card>
        </div>

        {/* Main Content */}
        <Tabs defaultValue="dashboard" className="space-y-6">
          <TabsList className="grid w-full grid-cols-4">
            <TabsTrigger value="dashboard">Dashboard</TabsTrigger>
            <TabsTrigger value="microfinances">Microfinances</TabsTrigger>
            <TabsTrigger value="members">Members</TabsTrigger>
            <TabsTrigger value="loans">Loans</TabsTrigger>
          </TabsList>

          <TabsContent value="dashboard" className="space-y-6">
            <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
              <Card>
                <CardHeader>
                  <CardTitle>Recent Activity</CardTitle>
                  <CardDescription>Latest member registrations and loan applications</CardDescription>
                </CardHeader>
                <CardContent>
                  <div className="space-y-4">
                    {members.length === 0 && loans.length === 0 ? (
                      <p className="text-muted-foreground">No recent activity</p>
                    ) : (
                      <div className="space-y-2">
                        {members.slice(-3).map(member => (
                          <div key={member.id} className="flex items-center justify-between p-2 bg-gray-50 rounded">
                            <span className="text-sm">New member: {member.firstName} {member.lastName}</span>
                            <Badge variant={member.status === 'Active' ? 'default' : 'secondary'}>
                              {member.status}
                            </Badge>
                          </div>
                        ))}
                        {loans.slice(-3).map(loan => (
                          <div key={loan.id} className="flex items-center justify-between p-2 bg-gray-50 rounded">
                            <span className="text-sm">Loan: KSh {loan.amount.toLocaleString()} - {loan.memberName}</span>
                            <Badge variant={loan.status === 'Pending' ? 'secondary' : 'default'}>
                              {loan.status}
                            </Badge>
                          </div>
                        ))}
                      </div>
                    )}
                  </div>
                </CardContent>
              </Card>

              <Card>
                <CardHeader>
                  <CardTitle>System Status</CardTitle>
                  <CardDescription>Current system health and statistics</CardDescription>
                </CardHeader>
                <CardContent>
                  <div className="space-y-4">
                    <div className="flex justify-between items-center">
                      <span className="text-sm font-medium">Database Status</span>
                      <Badge variant="default" className="bg-green-500">Online</Badge>
                    </div>
                    <div className="flex justify-between items-center">
                      <span className="text-sm font-medium">Total Records</span>
                      <span className="text-sm">{microfinances.length + members.length + loans.length}</span>
                    </div>
                    <div className="flex justify-between items-center">
                      <span className="text-sm font-medium">Last Updated</span>
                      <span className="text-sm">{new Date().toLocaleDateString()}</span>
                    </div>
                  </div>
                </CardContent>
              </Card>
            </div>
          </TabsContent>

          <TabsContent value="microfinances" className="space-y-6">
            <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
              <Card>
                <CardHeader>
                  <CardTitle>Create New Microfinance</CardTitle>
                  <CardDescription>Register a new SACCO organization</CardDescription>
                </CardHeader>
                <CardContent>
                  <form onSubmit={microfinanceForm.handleSubmit(onSubmitMicrofinance)} className="space-y-4">
                    <div>
                      <Label htmlFor="name">Name</Label>
                      <Input
                        id="name"
                        {...microfinanceForm.register('name', { required: true })}
                        placeholder="SACCO Name"
                      />
                    </div>
                    <div>
                      <Label htmlFor="description">Description</Label>
                      <Textarea
                        id="description"
                        {...microfinanceForm.register('description', { required: true })}
                        placeholder="Brief description of the SACCO"
                      />
                    </div>
                    <div>
                      <Label htmlFor="address">Address</Label>
                      <Input
                        id="address"
                        {...microfinanceForm.register('address', { required: true })}
                        placeholder="Physical address"
                      />
                    </div>
                    <div>
                      <Label htmlFor="phone">Phone</Label>
                      <Input
                        id="phone"
                        {...microfinanceForm.register('phone', { required: true })}
                        placeholder="Phone number"
                      />
                    </div>
                    <div>
                      <Label htmlFor="email">Email</Label>
                      <Input
                        id="email"
                        type="email"
                        {...microfinanceForm.register('email', { required: true })}
                        placeholder="Email address"
                      />
                    </div>
                    <Button type="submit" disabled={loading} className="w-full">
                      {loading ? 'Creating...' : 'Create Microfinance'}
                    </Button>
                  </form>
                </CardContent>
              </Card>

              <Card>
                <CardHeader>
                  <CardTitle>Existing Microfinances</CardTitle>
                  <CardDescription>List of all registered SACCOs</CardDescription>
                </CardHeader>
                <CardContent>
                  <div className="space-y-4">
                    {microfinances.length === 0 ? (
                      <p className="text-muted-foreground">No microfinances registered yet</p>
                    ) : (
                      microfinances.map(mf => (
                        <div key={mf.id} className="p-4 border rounded-lg">
                          <h4 className="font-semibold">{mf.name}</h4>
                          <p className="text-sm text-muted-foreground">{mf.description}</p>
                          <p className="text-sm text-muted-foreground">{mf.address}</p>
                          <div className="flex gap-4 mt-2 text-sm">
                            <span>üìû {mf.phone}</span>
                            <span>‚úâÔ∏è {mf.email}</span>
                          </div>
                        </div>
                      ))
                    )}
                  </div>
                </CardContent>
              </Card>
            </div>
          </TabsContent>

          <TabsContent value="members" className="space-y-6">
            <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
              <Card>
                <CardHeader>
                  <CardTitle>Register New Member</CardTitle>
                  <CardDescription>Add a new member to a microfinance</CardDescription>
                </CardHeader>
                <CardContent>
                  <form onSubmit={memberForm.handleSubmit(onSubmitMember)} className="space-y-4">
                    <div>
                      <Label htmlFor="microfinanceId">Microfinance</Label>
                      <Select onValueChange={(value) => memberForm.setValue('microfinanceId', value)}>
                        <SelectTrigger>
                          <SelectValue placeholder="Select microfinance" />
                        </SelectTrigger>
                        <SelectContent>
                          {microfinances.map(mf => (
                            <SelectItem key={mf.id} value={mf.id}>{mf.name}</SelectItem>
                          ))}
                        </SelectContent>
                      </Select>
                    </div>
                    <div className="grid grid-cols-2 gap-4">
                      <div>
                        <Label htmlFor="firstName">First Name</Label>
                        <Input
                          id="firstName"
                          {...memberForm.register('firstName', { required: true })}
                          placeholder="First name"
                        />
                      </div>
                      <div>
                        <Label htmlFor="lastName">Last Name</Label>
                        <Input
                          id="lastName"
                          {...memberForm.register('lastName', { required: true })}
                          placeholder="Last name"
                        />
                      </div>
                    </div>
                    <div>
                      <Label htmlFor="idNumber">ID Number</Label>
                      <Input
                        id="idNumber"
                        {...memberForm.register('idNumber', { required: true })}
                        placeholder="National ID number"
                      />
                    </div>
                    <div>
                      <Label htmlFor="phone">Phone</Label>
                      <Input
                        id="phone"
                        {...memberForm.register('phone', { required: true })}
                        placeholder="Phone number"
                      />
                    </div>
                    <div>
                      <Label htmlFor="email">Email</Label>
                      <Input
                        id="email"
                        type="email"
                        {...memberForm.register('email', { required: true })}
                        placeholder="Email address"
                      />
                    </div>
                    <div>
                      <Label htmlFor="address">Address</Label>
                      <Input
                        id="address"
                        {...memberForm.register('address', { required: true })}
                        placeholder="Physical address"
                      />
                    </div>
                    <Button type="submit" disabled={loading} className="w-full">
                      {loading ? 'Registering...' : 'Register Member'}
                    </Button>
                  </form>
                </CardContent>
              </Card>

              <Card>
                <CardHeader>
                  <CardTitle>Members List</CardTitle>
                  <CardDescription>All registered members</CardDescription>
                </CardHeader>
                <CardContent>
                  <div className="space-y-4 max-h-96 overflow-y-auto">
                    {members.length === 0 ? (
                      <p className="text-muted-foreground">No members registered yet</p>
                    ) : (
                      <Table>
                        <TableHeader>
                          <TableRow>
                            <TableHead>Name</TableHead>
                            <TableHead>SACCO</TableHead>
                            <TableHead>Status</TableHead>
                            <TableHead>Action</TableHead>
                          </TableRow>
                        </TableHeader>
                        <TableBody>
                          {members.map(member => (
                            <TableRow key={member.id}>
                              <TableCell>{member.firstName} {member.lastName}</TableCell>
                              <TableCell>{member.microfinanceName}</TableCell>
                              <TableCell>
                                <Badge variant={member.status === 'Active' ? 'default' : 'secondary'}>
                                  {member.status}
                                </Badge>
                              </TableCell>
                              <TableCell>
                                {member.status === 'Pending' && (
                                  <Button
                                    size="sm"
                                    onClick={() => activateMember(member.id)}
                                    disabled={loading}
                                  >
                                    Activate
                                  </Button>
                                )}
                              </TableCell>
                            </TableRow>
                          ))}
                        </TableBody>
                      </Table>
                    )}
                  </div>
                </CardContent>
              </Card>
            </div>
          </TabsContent>

          <TabsContent value="loans" className="space-y-6">
            <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
              <Card>
                <CardHeader>
                  <CardTitle>Apply for Loan</CardTitle>
                  <CardDescription>Submit loan application on behalf of member</CardDescription>
                </CardHeader>
                <CardContent>
                  <form onSubmit={loanForm.handleSubmit(onSubmitLoan)} className="space-y-4">
                    <div>
                      <Label htmlFor="memberId">Member</Label>
                      <Select onValueChange={(value) => loanForm.setValue('memberId', value)}>
                        <SelectTrigger>
                          <SelectValue placeholder="Select active member" />
                        </SelectTrigger>
                        <SelectContent>
                          {activeMembers.map(member => (
                            <SelectItem key={member.id} value={member.id}>
                              {member.firstName} {member.lastName} - {member.microfinanceName}
                            </SelectItem>
                          ))}
                        </SelectContent>
                      </Select>
                    </div>
                    <div>
                      <Label htmlFor="loanType">Loan Type</Label>
                      <Select onValueChange={(value) => loanForm.setValue('loanType', value)}>
                        <SelectTrigger>
                          <SelectValue placeholder="Select loan type" />
                        </SelectTrigger>
                        <SelectContent>
                          <SelectItem value="Emergency">Emergency</SelectItem>
                          <SelectItem value="Development">Development</SelectItem>
                          <SelectItem value="Business">Business</SelectItem>
                          <SelectItem value="Education">Education</SelectItem>
                        </SelectContent>
                      </Select>
                    </div>
                    <div>
                      <Label htmlFor="amount">Amount (KSh)</Label>
                      <Input
                        id="amount"
                        type="number"
                        {...loanForm.register('amount', { required: true, min: 1 })}
                        placeholder="Loan amount"
                      />
                    </div>
                    <div>
                      <Label htmlFor="repaymentPeriod">Repayment Period (months)</Label>
                      <Input
                        id="repaymentPeriod"
                        type="number"
                        {...loanForm.register('repaymentPeriod', { required: true, min: 1 })}
                        placeholder="Number of months"
                      />
                    </div>
                    <Button type="submit" disabled={loading} className="w-full">
                      {loading ? 'Submitting...' : 'Submit Loan Application'}
                    </Button>
                  </form>
                </CardContent>
              </Card>

              <Card>
                <CardHeader>
                  <CardTitle>Loan Applications</CardTitle>
                  <CardDescription>All loan applications</CardDescription>
                </CardHeader>
                <CardContent>
                  <div className="space-y-4 max-h-96 overflow-y-auto">
                    {loans.length === 0 ? (
                      <p className="text-muted-foreground">No loan applications yet</p>
                    ) : (
                      <Table>
                        <TableHeader>
                          <TableRow>
                            <TableHead>Member</TableHead>
                            <TableHead>SACCO</TableHead>
                            <TableHead>Type</TableHead>
                            <TableHead>Amount</TableHead>
                            <TableHead>Period</TableHead>
                            <TableHead>Status</TableHead>
                          </TableRow>
                        </TableHeader>
                        <TableBody>
                          {loans.map(loan => (
                            <TableRow key={loan.id}>
                              <TableCell>{loan.memberName}</TableCell>
                              <TableCell>{loan.microfinanceName}</TableCell>
                              <TableCell>{loan.loanType}</TableCell>
                              <TableCell>KSh {loan.amount.toLocaleString()}</TableCell>
                              <TableCell>{loan.repaymentPeriod} months</TableCell>
                              <TableCell>
                                <Badge variant={
                                  loan.status === 'Approved' ? 'default' : 
                                  loan.status === 'Rejected' ? 'destructive' : 'secondary'
                                }>
                                  {loan.status}
                                </Badge>
                              </TableCell>
                            </TableRow>
                          ))}
                        </TableBody>
                      </Table>
                    )}
                  </div>
                </CardContent>
              </Card>
            </div>
          </TabsContent>
        </Tabs>
      </div>
    </div>
  );
}
